.switch {
  text-align: center;
  background: #056353;
  border-radius: 25px;
  width: 150px;
  height: 50px;
  margin: -30px 0 0 -75px;
  font-size: 20px;
  transition: all .2s;
  position: absolute;
  top: 50%;
  left: 50%;
}

.switch span {
  background: #fff;
  width: 20px;
  height: 4px;
  margin: -2px 0 0 -4px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(-45deg);
}

.switch span:after {
  content: "";
  background: #fff;
  width: 4px;
  height: 12px;
  margin-top: -8px;
  transition: all .2s;
  display: block;
  position: absolute;
}

input[type="radio"] {
  display: none;
}

.switch label {
  cursor: pointer;
  color: #967b0596;
  width: 60px;
  line-height: 50px;
  transition: all .2s;
}

label[for="yes"] {
  height: 20px;
  position: absolute;
  left: 0;
}

label[for="no"] {
  position: absolute;
  right: 0;
}

#no:checked ~ .switch {
  background: #272120;
}

#no:checked ~ .switch span {
  background: #fff;
  margin-left: -8px;
}

#no:checked ~ .switch span:after {
  background: #fff;
  height: 20px;
  margin-top: -8px;
  margin-left: 8px;
}

#yes:checked ~ .switch label[for="yes"], #no:checked ~ .switch label[for="no"] {
  color: #fff;
}

